.finance_wrapper_comp_{
    padding-top: 55px;
    max-width: 1020px;
    margin: 0 auto;
}

.email-title_detail{
    color: #8d8d8d;
    font-weight: 300;
    line-height: 1;
    font: 300 16px/19px Open Sans,sans-serif;
    text-align: center;
    -webkit-font-smoothing: antialiased;
    box-sizing: border-box;
    font-family: Roboto,Arial,sans-serif;
    margin: 12px 0 3px 0;
    padding: 4px 0 0 0;
  }


._wrapper_com_{
    --color-tab--base--hover:var(--color-base--hover);
}

._wrapper_com_ .center_Tab_Nav_Items a {
    text-decoration: none;
}
.__items_setting.details {
    padding: 4px 3px;
}

.__items_setting.details .email-title_detail{
    padding: 14px 3px;
}


.__items_setting.details .filter_option.smt {
    display: flex;
    padding: 10px 14px 0px;
}

.__items_setting.details .filter_option.smt svg {
    padding: 7px 1px 7px 1px;
    fill: var(--hxrymz_color_base_label);
}

.__items_setting.details .filter_option.smt .scanCode svg {
    padding: 7px 1px 7px 15px;
    fill: var(--hxrymz_color_base_label);
}



.Wxwduf input{
    color: var(--hxrymz_color_base_label);
} 